import { useSpring, a } from 'react-spring/three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import * as THREE from 'three';
import {Canvas, useFrame} from "react-three-fiber";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { BoxBufferGeometry } from 'three';
import React, { useContext, useState, useEffect, Suspense, useRef} from 'react';
import {Html, useFBX, useGLTF} from 'drei';

const Model = () => {
  const gltf = useGLTF( '/sushi/scene.gltf', true)
    return <primitive object ={gltf.scene} dispose={null}/>



}

const Lights = ( ) => {

  return (
  <>
  
  <ambientLight intensity={1.1} />
  <spotLight intensity={1} position={[0,1000,0]} />
  


  </>
  );
}

const HTMLContent = ()  => {
  const ref = useRef();
  useFrame(() => (ref.current.rotation.y += 0.01));
  useFrame(() => (ref.current.rotation.x += 0.001));

  return (
    
      
        <mesh position ={[0,0,0]} ref={ref} >
          <Model/>
        </mesh>
        
     
      
    
   


  );
}




const Sushi = () => {

  
  

  
 

  return(
      <Canvas
      colorManagement
      camera ={{position: [0,6,0], fov: 70}}
      style={{height:500,width:400,margin:0,}}
      >
        <Lights/>
        <Suspense fallback={null}>
          <HTMLContent/>
        
        </Suspense>

         
     
    
   
      </Canvas>
  )
  
}



export default Sushi;